<template>
  <div id="bg-gray">
    <loading :active.sync="isLoading" loader="dots" :can-cancel="true" :is-full-page="fullPage"></loading>
    <div class="container py-5">
      <div class="row">
        <div class="col-md-4 text-center ">
          <span class="h4 my_title">1.</span>
        </div>
        <div class="col-md-4 text-center ">
          <span class="h4 no_title">2.</span>
        </div>
        <div class="col-md-4 text-center ">
          <span class="h4 no_title">3.</span>
        </div>
      </div>


      <div class="row mt-5">

        <!--  -->
        <div class="col-md-6">
          <table class="table my_list">
            <thead class="bg-secondary text-white">
              <th></th>
              <th></th>
              <th class="text-right"></th>
            </thead>
            <tbody>
              <tr v-for="(item, index) in cart.carts" :key="index">
                <td class="align-middle">
                  {{item.product.title}}
                  <div class="text-success" v-if="item.coupon">
                    
                  </div>
                </td>
                <td class="align-middle"> {{item.qty}} / {{item.product.unit}} </td>
                <td class="align-middle text-right"> {{item.final_total | NumCeiling |currency}} </td>
              </tr>
            </tbody>
            <tfoot>
              <tr>
                <td colspan="2" class="text-right"></td>
                <td class="text-right"> {{cart.total | NumCeiling |currency}} </td>
              </tr>
              <tr v-if="cart.final_total !== cart.total">
                <td colspan="2" class="text-right text-success"></td>
                <td class="text-right text-success"> {{cart.final_total | NumCeiling |currency}} </td>
              </tr>
            </tfoot>
          </table>

        </div>

        <!--  -->
        <form class="col-md-6 my_box" @submit.prevent="createOrder">
          <div class="form-group">
            <label for="useremail">Email</label>
            <input type="email" class="form-control" name="email" id="useremail" :class="{'is-invalid':errors.has('email')}"
              v-validate="'required|email'" v-model="form.user.email" placeholder=" Email">
            <span class="text-danger" v-if="errors.has('email')">{{ errors.first('email') }}</span>
          </div>

          <div class="form-group">
            <label for="username"></label>
            <input type="text" class="form-control" name="name" id="username" :class="{'is-invalid':errors.has('name')}"
              v-validate="'required'" v-model="form.user.name" placeholder="">
            <span class="text-danger" v-if="errors.has('name')"></span>
          </div>

          <div class="form-group">
            <label for="usertel"></label>
            <input type="tel" class="form-control" id="usertel" name="tel" :class="{'is-invalid':errors.has('tel')}"
              v-validate="'required'" v-model="form.user.tel" placeholder="">
            <span class="text-danger" v-if="errors.has('tel')"></span>
          </div>

          <div class="form-group">
            <label for="useraddress"></label>
            <input type="address" class="form-control" name="address" id="useraddress" :class="{'is-invalid':errors.has('address')}"
              v-validate="'required'" v-model="form.user.address" placeholder="">
            <span class="text-danger" v-if="errors.has('address')"></span>
          </div>

          <div class="form-group">
            <label for="useraddress"></label>
            <textarea name="" id="" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
          </div>
          <div class="text-right">
            <button class="btn btn-info"></button>
          </div>
        </form>
      </div>

    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        cart: {},
        form: {
          user: {
            name: '',
            email: '',
            tel: '',
            address: '',
          },
          message: '',
        },
        isLoading: false,
        fullPage: true,
        coupon_code: '',
      };
    },
    methods: {
      getCart() {
        const api = `${process.env.VUE_APP_APIPATH}/api/tingwankuo/cart`;
        const vm = this;
        vm.isLoading = true;
        this.$http.get(api).then((res) => {
          vm.cart = res.data.data;
          vm.isLoading = false;
        });
      },
      addCouponCode() {
        const api = `${process.env.VUE_APP_APIPATH}/api/tingwankuo/coupon`;
        const vm = this;
        vm.isLoading = true;
        let couponCode = {
          data: {
            code: vm.coupon_code
          }
        };
        this.$http.post(api, couponCode).then((res) => {
          // vm.$bus.$emit('messsagePush', res.data.message, 'success');
          vm.$notify({
            title: '',
            message: res.data.message,
            type: 'success'
          });
          vm.isLoading = false;
          this.getCart();
        });
      },
      createOrder() {
        const vm = this;
        const url = `${process.env.VUE_APP_APIPATH}/api/tingwankuo/order`;
        const order = vm.form;
        vm.isLoading = true;
        this.$validator.validate().then((res) => {
          if (res) {
            this.$http.post(url, {
              data: order
            }).then((res) => {
              vm.isLoading = false;
              this.$router.push(`/pay/${res.data.orderId}`);
            });
          } else {
            vm.isLoading = false;
          }
        });

      },
    },
    filters: {
      NumCeiling(num) {
        return Math.ceil(num);
      },
    },
    created() {
      this.getCart();
    },
  }
</script>

<style lang="scss" scoped>
  #bg-gray {
    background: rgb(243, 243, 243);
  }

  .my_box {
    background: #36679b;
    padding: 20px 20px;
    border-radius: 3px;
    color: white;
  }

  .my_list {
    background: rgb(230, 230, 230);
  }

  .my_title {
    color: white;
    background: #1aac98;
    border-radius: 4px;
    height: 40px;
    line-height: 40px;
    padding: 10px 30px;
  }

  .no_title {
    border-radius: 4px;
    height: 40px;
    line-height: 40px;
    padding: 10px 30px;
  }
</style>